import React, { useEffect, useState } from "react";

export default function Carousel() {
  const [n, setn] = useState(0);

  useEffect(() => {
    // console.log(n);
    const timer = setTimeout(() => {
      setn((n % 10) + 1);
    }, 3000);
    return () => {
      clearTimeout(timer);
    };
  }, [n]);

  return (
    <div id="carousel">
      <img
        src={`./images/${(n % 5) + 1}.png`}
        className="car-img h-100 w-100"
        alt="School Building"
      />
    </div>
  );
}
